.crafeloBody {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20rem 0;
  height: 80vh;

  &__left {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
      font-style: normal;
      font-weight: bold;
      font-size: 4.2rem;
      font-family: poppins;
      line-height: 6.2rem;
      color: white;

      span {
        color: #fecd00;
      }
    }

    h4 {
      font-family: Poppins;
      font-style: normal;
      font-weight: normal;
      font-size: 1.8rem;
      line-height: 2.7rem;
      color: rgba(white, 0.8);
    }

    &--sub {
      display: flex;
      flex-direction: column;

      p {
        margin-bottom: 1rem;
        color: rgba(255, 150, 91, 0.8);
        font-family: Poppins;
        font-style: normal;
        font-weight: normal;
        font-size: 1.6rem;
        line-height: 24px;
      }

      &-form {
        display: flex;
        flex-direction: row;
        width: 83%;
        justify-content: space-between;
        align-items: center;

        input {
          width: 34rem;
          height: 5rem;
          border: none;
          border-radius: 1rem;
          padding: 0.8rem 0.8rem 0.8rem 2rem;

          &:placeholder-shown {
            font-family: Poppins;
            font-style: normal;
            font-weight: normal;
            font-size: 1.6rem;
            line-height: 2.4rem;
          }

          &:focus {
            outline: none;
          }
        }

        button {
          background: #fecd00;
          border: none;
          border-radius: 1rem;
          height: 5rem;
          width: 15rem;
          padding: 0.8rem;
          font-family: Poppins;
          font-style: normal;
          font-weight: normal;
          font-size: 1.6rem;
          line-height: 2.4rem;
          transition: all 0.2s;

          &:hover {
            cursor: pointer;
            transform: scale(1.05);
          }

          &:active {
            transform: scale(0.98);
          }
        }
      }
    }
  }

  &__right {
    display: grid;
    align-content: center;
    justify-items: space-between;
    height: 100%;
    grid-column-gap: 0.6rem;
    grid-row-gap: 0.3rem;
    width: 50%;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(3, 1fr);
    // padding: 0 3rem;
    margin-left: 2rem;

    img {
      object-fit: cover;
      height: 100%;
      width: 100%;
      border-radius: 1rem;
    }

    &--1 {
      grid-column: 1 / span 4;
      grid-row: 1 / span 3;
    }

    &--2 {
      grid-column: 5 / span 2;
      grid-row: 1 / 2;
    }

    &--3 {
      grid-column: 5 / span 2;
      grid-row: 3/ 4;
    }

    &--4 {
      grid-column: 1 / span 2;
      grid-row: 5 / 6;
    }

    &--5 {
      grid-column: 3 / span 2;
      grid-row: 5 / 6;
    }

    &--6 {
      grid-column: 5 / span 2;
      grid-row: 5 / 6;
    }
  }
}
